import LineChart from '@/components/LineChart/LineChart';
import DateRangePicker from '@/components/DateRangePicker/DateRangePicker';
import autotime from '@/utils/autotime';

var moment = require('moment');

import { detail } from '@/api/channel.js';
export default {
  components: {
    DateRangePicker: DateRangePicker,
    LineChart: LineChart
  },
  props: {
    url: {
      type: String,
      default: ''
    }
  },
  data: function data() {
    return {
      config: [{
        index: 'pv',
        title: '浏览量',
        value: 0
      }, {
        index: 'uv',
        title: '访客数',
        value: 0
      }, {
        index: 'pv_pay',
        title: '支付订单',
        value: 0
      }, {
        index: 'uv_pay',
        title: '支付人数',
        value: 0
      }, {
        index: 'gmv_fee',
        title: '支付金额',
        value: 0
      }],
      currentId: 0,
      usergroup: 'chan_all',
      lineChartData: {
        tit: '',
        actualData: [],
        xdate: [],
        date: '浏览量'
      },
      dates: [],
      lineChartArr: {},
      loading: false
    };
  },
  created: function created() {
    autotime.reloadTime();
    this.dates = [autotime.y7day, autotime.yday];
    this.refreshCharts();
  },
  methods: {
    refreshCharts: function refreshCharts() {
      var _this = this;

      var mts = moment(this.dates[0]);
      var mte = moment(this.dates[1]);
      this.loading = true;
      detail({
        gid: this.usergroup,
        url: this.url,
        indices: ['pv', 'uv', 'pv_pay', 'uv_pay', 'gmv_fee'],
        sts: mts.unix(),
        ets: mte.unix() + 86400
      }).then(function (res) {
        console.log(res); // console.log(this.config)

        _this.loading = false;

        _this.config.forEach(function (v) {
          v.value = res.data[v.index] || 0;
        });

        console.log(_this.config);

        _this.getChartData(mts, mte);
      }).catch(function (e) {
        _this.loading = false;
      });
    },
    getChartData: function getChartData(mts, mte) {
      var _this2 = this;

      var data = {
        gid: this.usergroup,
        url: this.url,
        indices: ['pv', 'uv', 'pv_pay', 'uv_pay', 'gmv_fee'],
        sts: mts.unix(),
        ets: mte.unix() + 86400
      };

      if (mts.isSame(mte)) {
        data.time_group = 'hh';
        this.lineChartData.xdate = autotime.hours;
      } else if (mts.isBefore(mte)) {
        data.time_group = 'ds';
        this.lineChartData.xdate = [];

        while (!mts.isAfter(mte)) {
          this.lineChartData.xdate.push(mts.format('YYYY-MM-DD'));
          mts.add(1, 'd');
        }
      } else {
        return;
      }

      this.loading = true;
      detail(data).then(function (res) {
        if (data.time_group === 'hh') {
          _this2.lineChartArr = {};
          var result = res.data[mts.format('YYYY-MM-DD')];
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (h, i) {
              _this2.lineChartArr[v].push(result && result[i.toString()] && result[i.toString()][v] || 0);
            });
          });
        } else {
          _this2.lineChartArr = {};
          data.indices.forEach(function (v) {
            _this2.lineChartArr[v] = [];

            _this2.lineChartData.xdate.forEach(function (i) {
              _this2.lineChartArr[v].push(res.data[i] && res.data[i][v] || 0);
            });
          });
        }

        _this2.choose(_this2.currentId);

        _this2.loading = false;
      }).catch(function (e) {
        _this2.loading = false;
      });
    },
    choose: function choose(i) {
      this.currentId = i;
      this.lineChartData.date = this.config[i].title;
      this.lineChartData.actualData = this.lineChartArr[this.config[i].index];
    }
  }
};